const useDateDmy = () => {
    const months = [
        "Januari",
        "Februari",
        "Maret",
        "April",
        "Mei",
        "Juni",
        "Juli",
        "Agustus",
        "September",
        "Oktober",
        "November",
        "Desember"
    ]

    const create = (dateStr: string) => {
        if (!dateStr) return
        const [Y, m, d] = dateStr.split("-")
        let month: string | number = parseInt(m) - 1
        month = months[month]
        return `${parseInt(d)} ${month} ${Y}`
    }
    return create
}
export default useDateDmy
