import { memo } from "react"
import useDateDmy from "../../Hooks/useDateDmy"
import useJenisKelamin from "../../Hooks/useJenisKelamin"

const EditSktm: React.FC<{
    dataSurat: DataT
    formControlInputSurat: string
}> = ({ dataSurat, formControlInputSurat }) => {
    const jkParsing = useJenisKelamin()
    const dateDmyBuilder = useDateDmy()
    return (
        <>
            <section id="isi-surat">
                <table>
                    <tbody>
                        <tr>
                            <td colSpan={3}>
                                <p className="paragraf">
                                    Yang bertanda tangan di bawah ini, Kepala
                                    Kelurahan Jembatan Kecil Kecamatan Singaran
                                    Pati Kota Bengkulu, dengan ini menerangkan
                                    dengan sebenarnya bahwa:
                                </p>
                            </td>
                        </tr>
                        <tr>
                            <td className="label">
                                <p>Nama</p>
                                <p>Tempat, Tanggal Lahir</p>
                                <p>Jenis Kelamin</p>
                                <p>NIK/No. KTP</p>
                                <p>Pendidikan Terakhir</p>
                                <p>Status Perkawinan</p>
                                <p>Pekerjaan</p>
                                <p className="alamat">Alamat</p>
                            </td>
                            <td className="pembatas">
                                <p>:</p>
                                <p>:</p>
                                <p>:</p>
                                <p>:</p>
                                <p>:</p>
                                <p>:</p>
                                <p>:</p>
                                <p className="alamat">:</p>
                            </td>
                            <td className="data-surat">
                                <p>{dataSurat.orang_tua.nama_lengkap}</p>
                                <p>{`${
                                    dataSurat.orang_tua.tempat_lahir ||
                                    "Bengkulu"
                                }, ${dateDmyBuilder(
                                    dataSurat.orang_tua.tanggal_lahir ||
                                        "2000-01-01"
                                )}`}</p>
                                <p>
                                    {jkParsing(
                                        dataSurat.orang_tua.jenis_kelamin
                                    )}
                                </p>
                                <p>{dataSurat.orang_tua.nik}</p>
                                <p>{dataSurat.orang_tua.pendidikan}</p>
                                <p>{dataSurat.orang_tua.status_perkawinan}</p>
                                <p>{dataSurat.orang_tua.pekerjaan}</p>
                                <p className="alamat">
                                    {dataSurat.orang_tua.alamat}
                                </p>
                            </td>
                        </tr>
                        <tr>
                            <td className="ket-surat" colSpan={3}>
                                <p
                                    className={formControlInputSurat}
                                    data-name="keterangan"
                                    contentEditable
                                    suppressContentEditableWarning
                                >
                                    {`${dataSurat.keterangan}:`}
                                </p>
                            </td>
                        </tr>
                        <tr>
                            <td className="label">
                                <p>Nama</p>
                                <p>Jenis Kelamin</p>
                                <p>Tempat Tanggal Lahir</p>
                                <p>Pekerjaan</p>
                                <p>Sekolah/Universitas</p>
                                <p>Semester/Kelas</p>
                            </td>
                            <td className="pembatas">
                                <p>:</p>
                                <p>:</p>
                                <p>:</p>
                                <p>:</p>
                                <p>:</p>
                                <p>:</p>
                            </td>
                            <td className="data-surat">
                                <p>{dataSurat.surat.pemohon.nama}</p>
                                <p>
                                    {jkParsing(
                                        dataSurat.surat.pemohon.jenis_kelamin
                                    )}
                                </p>
                                <p>{`${
                                    dataSurat.surat.pemohon.tempat_lahir ||
                                    "Bengkulu"
                                }, ${dateDmyBuilder(
                                    dataSurat.surat.pemohon.tanggal_lahir ||
                                        "2000-01-01"
                                )}`}</p>
                                <p>{dataSurat.surat.pemohon.jenis_pekerjaan}</p>
                                <p
                                    contentEditable
                                    suppressContentEditableWarning
                                    className={formControlInputSurat}
                                    data-name="pendidikan"
                                >
                                    {dataSurat.pendidikan}
                                </p>
                                <p>{dataSurat.pendidikan}</p>
                            </td>
                        </tr>
                        <tr>
                            <td className="ket-surat" colSpan={3}>
                                <p
                                    className={formControlInputSurat}
                                    data-name="keperluan"
                                    contentEditable
                                    suppressContentEditableWarning
                                >
                                    {dataSurat.keperluan}
                                </p>
                            </td>
                        </tr>
                    </tbody>
                </table>
            </section>
        </>
    )
}

export default memo(EditSktm)
